.header {
  background-color: var(--background-color);
  outline: 1px solid #ff0;
}

.trainers {
  background-color: var(--background-color);
  padding-top: 50px;
  padding-bottom: 50px;
}

.trainers__photo {
  width: 300px;
}

.footer {
  outline: 1px solid #ff0;
}

.d-block {
  width: 300px;
}

.carousel-inner {
  width: 100%;
  height: 100%;
}

.trainers-section {
  color: #222;
  text-align: center;
  background: #f6f7f9;
  padding: 64px 20px;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica Neue, Arial;
}

.trainers-section__title {
  margin: 0 0 28px;
  font-size: 28px;
  font-weight: 700;
}

.trainers-section__viewport {
  align-items: center;
  gap: 12px;
  max-width: 920px;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.trainers-section__arrow {
  cursor: pointer;
  background: #fff;
  border: 0;
  border-radius: 20%;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  transition: transform .18s, background .18s;
  display: inline-flex;
  box-shadow: 0 6px 20px #12121814;
}

.trainers-section__arrow:hover {
  background: #f0f0f0;
  transform: scale(1.06);
}

.trainers-section__arrow svg {
  fill: none;
  stroke: #222;
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
  width: 22px;
  height: 22px;
}

.trainers-section__track-wrapper {
  flex: auto;
  overflow: hidden;
}

.trainers-section__track {
  will-change: transform;
  margin: 0;
  padding: 0;
  list-style: none;
  transition: transform .6s cubic-bezier(.22, .9, .35, 1);
  display: flex;
}

.trainers-section__slide {
  box-sizing: border-box;
  justify-content: center;
  min-width: 100%;
  padding: 0 20px;
  display: flex;
}

.trainer-card {
  text-align: left;
  background: #fff;
  border-radius: 16px;
  align-items: flex-start;
  gap: 26px;
  width: 100%;
  max-width: 680px;
  padding: 34px;
  display: flex;
  box-shadow: 0 10px 30px #1018280f;
}

.trainer-card__photo {
  object-fit: cover;
  border: 4px solid #f1f1f3;
  border-radius: 20%;
  flex: 0 0 160px;
  width: 160px;
  height: 160px;
}

.trainer-card__name {
  color: #111;
  margin: 0 0 8px;
  font-size: 20px;
  font-weight: 700;
}

.trainer-card__text {
  color: #444;
  max-height: 220px;
  margin: 0;
  padding-right: 6px;
  line-height: 1.55;
  overflow-y: auto;
}

.trainers-section__dots {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  display: flex;
}

.trainers-section__dots .dot {
  cursor: pointer;
  background: #cfcfcf;
  border-radius: 60%;
  width: 12px;
  height: 12px;
  transition: transform .18s, background .18s;
}

.trainers-section__dots .dot.active {
  background: #222;
  transform: scale(1.18);
}

@media (width <= 820px) {
  .trainer-card {
    text-align: center;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    padding: 22px;
  }

  .trainer-card__photo {
    flex: 0 0 140px;
    width: 140px;
    height: 140px;
  }

  .trainer-card__text {
    max-height: 180px;
  }
}

@media (width <= 520px) {
  .trainers-section__arrow {
    border-radius: 15%;
    width: 40px;
    height: 40px;
    display: inline-flex;
  }

  .trainer-card__photo {
    flex: 0 0 120px;
    width: 120px;
    height: 120px;
  }

  .trainer-card {
    text-align: center;
    flex-direction: column;
    align-items: center;
    gap: 12px;
    padding: 18px;
  }

  .trainer-card__text {
    max-height: 160px;
  }
}
